.wrapper {
  display: flex;
  align-items: flex-start;
  width: 1200px;
  margin: 60px auto;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
}

.nav {
  width: 320px;
}

.list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.listItem {
  cursor: pointer;
}

.active {
  color: aliceblue;
}

a {
  text-decoration: none;
}
